.paper-container{
    margin-bottom: 20px;
    cursor: pointer;
    height: 400px;
    width: 300px ; 
    box-sizing: border-box;
}
.paper { 
    position: relative;
}

.poster-image { 
    height: 400px;
    width: 300px ; 
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.7);
    border-radius: 10px;
}
.poster-darker{
    position: absolute;
    bottom: 9px ; 
    left: 0;
    height: 20%;
    width: 100% ; 
    background-color: rgba(0, 0, 0, .8) ; 
    border-bottom-left-radius: 10px ; 
    border-bottom-right-radius: 10px;
    
}
.posters-container{
    margin-top: 30px;
    align-items: center;
    width: 100%;
    margin-left : 50% ; 
    transform: translate( -50% , 0%);

}


.poster-year :hover { 
    display: inline
}

.poster-year { 
    position: absolute;
    bottom: 10px ;
    left : 50% ; 
    transform: translate(-50% , 0);
    font-weight: bold; 
    font-size: 40px;
    padding-left: 20px ; 
    padding-right: 20px ; 
    color: aliceblue;
   

}
.sher-box-container2{
    position: relative;
      margin-top: 300px;
      max-width: 1500px;
      display: flex;
      margin: 0 auto;
      justify-content: center;
}

  /* Float four columns side by side */
.columns {
    float: left;
    width: 300px;
    margin-left: 10px;
    margin-right: 10px;

}

/* Remove extra left and right margins, due to padding */
.row3 {
    margin-top: 100px;
    display: grid ; 
    grid-template-columns:auto auto auto auto;
   
}

/* Clear floats after the columns */
.row3:after {
    content: "";
    display: table;
    clear: both;
}



@media only screen and (max-width: 1318px) {
    .row3{
        grid-template-columns:auto auto auto;
    }
  
}

@media only screen and (max-width: 1000px) {
    .row3{
        grid-template-columns:auto auto;
    }
  
}

@media only screen and (max-width: 660px) {
    .row3{
        grid-template-columns: auto;
    }
  
}